  margin-bottom: 0;
  overflow: hidden;
}

.table {
  /* width: 100%;
    margin-bottom: 1rem; */
  color: #212529;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.custom-table thead th {
  background-color: #f7fafe;
  vertical-align: middle;
  height: 72px;
  border-bottom: none !important;
  color: #111;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  padding: 0;
  text-align: center;
  vertical-align: middle;
  border: 0 none;
}

.custom-table.table-striped tbody tr:nth-of-type(2n + 1) {
  background-color: #ffffff;
}

.custom-table tbody tr:first-child td {
  border-top: 0;
}

.custom-table tbody .first-column {
  width: 45%;
}

.custom-table tbody td .title {
  color: #111;
  font-size: 18px;
  font-weight: 500;
